import React from 'react';
import { Component } from 'react';
import { render } from 'react-dom';
import Gallery from 'react-photo-gallery';
import Lightbox from 'react-images';




import portfolio_11_1 from '../img/portfolio_11/portfolio_11_1.jpg';
import portfolio_11_2 from '../img/portfolio_11/portfolio_11_2.jpg';
import portfolio_11_3 from '../img/portfolio_11/portfolio_11_3.jpg';
import portfolio_11_4 from '../img/portfolio_11/portfolio_11_4.jpg';
import portfolio_11_5 from '../img/portfolio_11/portfolio_11_5.jpg';
import portfolio_11_6 from '../img/portfolio_11/portfolio_11_6.jpg';





const photos = [
  { src: portfolio_11_1, width: 1, height: 1 },
  { src: portfolio_11_2, width: 1, height: 1 },
  { src: portfolio_11_3, width: 1, height: 1 },
  { src: portfolio_11_4, width: 1, height: 1 },
  { src: portfolio_11_5, width: 1, height: 1 },
  { src: portfolio_11_6, width: 1, height: 1 }




];




class Portfolio_11 extends Component {
					constructor() {
					super();
					this.state = { currentImage: 0 };
					this.closeLightbox = this.closeLightbox.bind(this);
					this.openLightbox = this.openLightbox.bind(this);
					this.gotoNext = this.gotoNext.bind(this);
					this.gotoPrevious = this.gotoPrevious.bind(this);
				}
				openLightbox(event, obj) {
					this.setState({
						currentImage: obj.index,
						lightboxIsOpen: true,
					});
				}
				closeLightbox() {
					this.setState({
						currentImage: 0,
						lightboxIsOpen: false,
					});
				}
				gotoPrevious() {
					this.setState({
						currentImage: this.state.currentImage - 1,
					});
				}
				gotoNext() {
					this.setState({
						currentImage: this.state.currentImage + 1,
					});
				}


	render() {
		return(
			<div className="img__margin">
			<div className="container"></div>
			 <Gallery photos={photos} onClick={this.openLightbox} />
			 <Lightbox images={photos}
				 onClose={this.closeLightbox}
				 onClickPrev={this.gotoPrevious}
				 onClickNext={this.gotoNext}
				 currentImage={this.state.currentImage}
				 isOpen={this.state.lightboxIsOpen}
			 />
		 </div>

		)

	return (
		<div className="preloader">
			PRELOADER
		</div>
	)
}
}




export default Portfolio_11;
