import React from 'react';
import { Component } from 'react';
import { render } from 'react-dom';
import Gallery from 'react-photo-gallery';
import Lightbox from 'react-images';

import './style.css';







import portfolio_5_1 from '../img/portfolio_5/portfolio_5_1.jpg';
import portfolio_5_2 from '../img/portfolio_5/portfolio_5_2.jpg';
import portfolio_5_3 from '../img/portfolio_5/portfolio_5_3.jpg';
import portfolio_5_4 from '../img/portfolio_5/portfolio_5_4.jpg';
import portfolio_5_5 from '../img/portfolio_5/portfolio_5_5.jpg';
import portfolio_5_6 from '../img/portfolio_5/portfolio_5_6.jpg';
import portfolio_5_7 from '../img/portfolio_5/portfolio_5_7.jpg';
import portfolio_5_8 from '../img/portfolio_5/portfolio_5_8.jpg';
import portfolio_5_9 from '../img/portfolio_5/portfolio_5_9.jpg';
import portfolio_5_10 from '../img/portfolio_5/portfolio_5_10.jpg';





const photos = [
  { src: portfolio_5_1, width: 1, height: 1 },
  { src: portfolio_5_2, width: 1, height: 1 },
  { src: portfolio_5_3, width: 1, height: 1 },
  { src: portfolio_5_4, width: 1, height: 1 },
  { src: portfolio_5_5, width: 1, height: 1 },
  { src: portfolio_5_6, width: 1, height: 1 },
  { src: portfolio_5_7, width: 1, height: 1 },
  { src: portfolio_5_8, width: 1, height: 1 },
  { src: portfolio_5_9, width: 1, height: 1 },
	{ src: portfolio_5_10, width: 1, height: 1 }

];




class Portfolio_5 extends Component {
					constructor() {
					super();
					this.state = { currentImage: 0 };
					this.closeLightbox = this.closeLightbox.bind(this);
					this.openLightbox = this.openLightbox.bind(this);
					this.gotoNext = this.gotoNext.bind(this);
					this.gotoPrevious = this.gotoPrevious.bind(this);
				}
				openLightbox(event, obj) {
					this.setState({
						currentImage: obj.index,
						lightboxIsOpen: true,
					});
				}
				closeLightbox() {
					this.setState({
						currentImage: 0,
						lightboxIsOpen: false,
					});
				}
				gotoPrevious() {
					this.setState({
						currentImage: this.state.currentImage - 1,
					});
				}
				gotoNext() {
					this.setState({
						currentImage: this.state.currentImage + 1,
					});
				}


	render() {
		return(
			<div className="img__margin">
			<div className="container"></div>
			 <Gallery photos={photos} onClick={this.openLightbox} />
			 <Lightbox images={photos}
				 onClose={this.closeLightbox}
				 onClickPrev={this.gotoPrevious}
				 onClickNext={this.gotoNext}
				 currentImage={this.state.currentImage}
				 isOpen={this.state.lightboxIsOpen}
			 />
		 </div>

		)

	return (
		<div className="preloader">
			PRELOADER
		</div>
	)
}
}




export default Portfolio_5;
