
import React, { Component } from 'react';
import { render } from 'react-dom';

import Dropdown_box from '../Dropdown_box';
import Header228 from '../header228';

import './style.css';


class HoverExample extends Component {
  constructor(props) {
    super(props);
    this.handleMouseHover = this.handleMouseHover.bind(this);
    this.state = {
      isHovering: false,
    };
  }

  handleMouseHover() {
    this.setState(this.toggleHoverState);
  }

  toggleHoverState(state) {
    return {
      isHovering: !state.isHovering,
    };
  }

  render() {
    return (
<div>
			<div className="all_line_box">

			<div className="my_first_line__wrapp">
					<div className="my_first__line">
							<div className="dropdown_but">

					<div className="hov"
					 	onMouseEnter={this.handleMouseHover}
					 	onMouseLeave={this.handleMouseHover}
				 		>
				 		<h6>наши услуги</h6>
			 		</div>

					</div>
					<div className="breadcrumbs_box">

					</div>




					</div>
			</div>

			<div className="my_second_line__wrapp">
					<div className="my_second__line">

					</div>
			</div>

			</div>















        {this.state.isHovering &&

				<Dropdown_box />
					}



</div>
    );
  }
}

export default HoverExample ;
